nav {
  position: absolute;
  top: 0;
  width: 100%;
  height: 0;
  background: #000;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 100;
}

nav ul {
  list-style: none;
}

nav ul li {
  margin: 50px 0;
}

nav ul li a {
  color: #fafafa;
  font-size: 2em;
  font-family: "Josefin Sans", sans-serif;
  text-decoration: none;
  font-weight: 600;
  position: relative;
  opacity: 0;
  pointer-events: none;
}

nav ul li a:after {
  content: "";
  content: "";
  width: 100%;
  position: absolute;
  height: 3px;
  border-radius: 5px;
  background: #fff;
  bottom: -10px;
  left: 0;
  -webkit-transform-origin: left;
          transform-origin: left;
  transition: -webkit-transform 0.5s ease;
  transition: transform 0.5s ease;
  transition: transform 0.5s ease, -webkit-transform 0.5s ease;
  -webkit-transform: scaleX(0);
          transform: scaleX(0);
}

nav ul li a:hover:after {
  -webkit-transform: scaleX(1);
          transform: scaleX(1);
}

nav .close {
  width: 100%;
  height: 100vh;
  cursor: pointer;
  opacity: 0;
  pointer-events: none;
}
nav .close div{
  position: absolute;
  top: 7%;
  right: 14%;
}




nav .close div::before,
nav .close div::after {
  content: "";
  position: absolute;
  width: 30px;
  height: 4px;
  background: #fff;
  transition: background 0.5s ease;
}
nav .close div::before {
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
}

nav .close div::after {
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}

nav .close:hover div::before,
nav .close:hover div::after {
  background: rgb(209, 3, 3);
}

